<script>
import ImageZoom from "./ImageZoom";
import { mapGetters } from "vuex";
export default {
  name: "BaseDetail",
  data() {
    return {
      currentIndex: 0,
      count: 1,
      goodAttribute: [],
    };
  },
  components: { ImageZoom },
  computed: {
    // 获取getters中的数据
    ...mapGetters("detailOptions", [
      "spuSaleAttrList",
      "skuInfo",
      "categoryView",
    ]),
  },
  methods: {
    // 添加购物车
    async addInCart() {
      // 获取用户选择的属性
      let arr = this.$refs.attrItem.filter((item) => {
        return item.getAttribute("class") == "active";
      });
      arr.forEach((item) => {
        console.log(this);
        this.goodAttribute.push(item.textContent);
      });
      try {
        await this.$store.dispatch("cartOptions/AddCartList", {
          goodAttribute: this.goodAttribute,
          skuId: this.goodId,
          skuNum: this.count,
          skuInfo: this.skuInfo,
        });
        this.$router.push({
          name: "AddCart",
          params: {
          goodAttribute: this.goodAttribute,
          skuNum: this.count,
          skuInfo: this.skuInfo,
        },
        });
      } catch (error) {}
    },
    // 排他操作
    changeChecked(attrValueList, arr) {
      arr.forEach((item) => {
        item.isChecked = false;
      });
      attrValueList.isChecked = true;
    },
  },
  // 接受商品id
  props: ["goodId"],
  mounted() {
    this.$store.dispatch("detailOptions/GetGoodItem", this.goodId);
  },
};
</script>

